import axios from 'axios';
import store from '@/store';
import router from '@/router';

let instance;

const axiosInstance = (urlMethod) => {
  if (!instance) {
    instance = axios.create({
      timeout: 10000,
      withCredentials: true,
      responseType: 'json',
    });
  }
  instance.defaults.headers.Appnest_Method = urlMethod;
  instance.interceptors.request.use((request) => {
    store.state.isloadingShow = true;
    return request;
  });
  instance.interceptors.response.use(
    (response) => {
      store.state.isloadingShow = false;

      return response.data ? response.data : response;
    }, (err) => {
      if (err.response && err.response.status === 405) {
        router.push('/loginRegister/login');
        store.state.isloadingShow = false;
      }
    },
  );

  return instance;
};

const host = "http://uc.apron.network:8880";

const get = urlMethod => {urlMethod = host + urlMethod; return axiosInstance(urlMethod).get(urlMethod);}

const post = (urlMethod, payload) => {urlMethod = host + urlMethod; return axiosInstance(urlMethod).post(urlMethod, payload);}


export default {
  get,
  post,
};
